.container {
border: 2px solid #dedede;
background-color: #f1f1f1;
border-radius: 5px;
padding: 10px;
margin: 10px 0;

}

.darker {
    border-color: #ccc;
    background-color: #ddd;
  }

  .container::after {
    content: "";
    clear: both;
    display: table;
  }
  
  /* Style images */
  .container img {
    float: left;
    max-width: 60px;
    width: 100%;
    margin-right: 20px;
    border-radius: 50%;
  }
  

  .container .imgRight {
    float: right;
    margin-left: 20px;
    margin-right:0;
  }
  
  /* Style time text */
  .timeRight {
    float: right;
    color: #aaa;
  }
  
  /* Style time text */
  .timeLeft {
    float: left;
    color: #999;
  }